.sidebar-nav {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	position: fixed;
	right: 0;
	padding-right: 5%;
	bottom: 10%;
	background-color: transparent;
}

.rs-sidenav-subtle {
	background-color: transparent !important;
}

.sidebar-links {
	display: flex;
	flex-direction: column;
	text-align: right;
	font-family: 'DungGeunMo', sans-serif;
	letter-spacing: 0em;
	line-height: 1.6em;
	font-size: 22px;
	padding: 0.2em;
	font-weight: bold;
}

a {
	text-decoration: none !important;
	color: var(--green-bright) !important;
}

a:hover {
	color: var(--lightest-slate) !important;
}

.sidebar-logos {
	padding-top: 20px;
	width: 110px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding-bottom: 8px;
}

.sidebar-logos a {
	padding-right: 15px;
}

img {
	height: 18px;
}

@media only screen and (max-width: 600px) {
	.sidebar-nav {
		width: 100%;
		height: 50px;
	}

	.sidebar-links {
		padding-left: 20px;
		flex-direction: row;
		text-align: right;
		font-size: 9px;
	}

	.sidebar-logos {
		padding-top: 0px;
		width: 100px;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		padding-left: 50%;
	}

	img {
		height: 12px;
	}
}

@media only screen and (max-width: 600px) {
	.sidebar-nav {
		position: unset;
		align-items: center;
	}
	.sidebar-logos {
		width: 70px;
		padding-left: unset;
	}
}
